.preview {
  height: 100vh;
  width: 100%;
  // background-image: url(../../images/fondo-5.jpg);
  background-image: url(../../images/fondo-fc.webp);
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include tablet() {
    height: 100vh;
    width: 100%;
    border-radius: 6px;
  }
  @include desktop {
    height: 100vh;
    width: 100%;
  }
}

.preview__reset {
  width: 75px;
  height: 30px;
  border-radius: 15px;
  border: solid 1px $color-white-two;
  background-color: transparent;
  color: $color-gunmetal;
  font-size: 12px;
  cursor: pointer;
}

.cards__reset--text {
  text-transform: uppercase;
  font-family: $font-text;
  padding: 0 5px;
}

